<template>
	<div id="joinS">
		<h3>Howdy</h3>
		<div class="row">
			<router-link to="/about">
				<button class="btn"><p id="why-paragraph">{{ $t('whyJoinUs') }}</p></button>
			</router-link>
			<form @submit.prevent="join" class="col s12">
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="name" required>
						<label>Name</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="email" required>
						<label>Email</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="phone" required>
						<label>Phone</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="wantsTo" required>
						<label>Wanted department</label>
					</div>
				</div>
				<button @click="afterSubmit" type="submit" class="btn">Submit</button>
				<router-link to="/" class="btn grey">Cancel</router-link>
			</form>

		</div>
	</div>
</template>
<script>
	import db from './firebaseInit'
	export default {
		name: 'join-employee',
		data() {
			return {
				email: null,
				phone: null,
				name: null,
				wantsTo: null
			}
		},
		methods: {
			join() {
				db.collection('join_wishers').add({
					email: this.email,
					name: this.name,
					phone: this.phone,
					wantsTo: this.wantsTo
				})
				.then(docRef => this.$router.push('/'))
				.catch(error => console.log(err))
			},
			afterSubmit() {

			}
		}

	}
</script>
<style>
	#why-paragraph {
		font-size: 15px;
		margin-top: 3px;
	}
</style>


