<template>
  <div id="app">
    <Navbar/>
    <div class="container">
    <router-view/>
    <div @click="toggleClicked" v-bind:class="{ 'clicked': languageToggleClicked }">
    <button id="languageToggler"v-for="entry in languages" :key="entry.title" @click="changeLocale(entry.language)">
        <flag :iso="entry.flag" v-bind:squared=false /> {{entry.title}}
    </button>
    </div>
    </div>
  </div>
</template>

<script>
import Navbar from './components/Navbar'
import i18n from '@/plugins/i18n';
export default {
  name: 'App',
  components: {
    Navbar
  },
  data() {
    return {
      languageToggleClicked: false,
      languages: [
            { flag: 'us', language: 'en', title: 'English' },
            { flag: 'ru', language: 'ru', title: 'Русский' }
        ]
    }
  },
  methods: {
    changeLocale(locale) {
        i18n.locale = locale;
    },
    toggleClicked() {
      this.languageToggleClicked = true
    }
}
}
</script>
<style>
#languageToggler{
  margin-left: 10rem;
  margin-top: 50px;
  background:#1AAB8A;
  color:#fff;
  border:none;
  position:relative;
  height:50px;
  font-size:1.6em;
  padding:0 2em;
  cursor:pointer;
  transition:800ms ease all;
  outline:none;
}
#languageToggler:hover{
  background:#fff;
  color:#1AAB8A;
}
#languageToggler:before,#languageToggler:after{
  content:'';
  position:absolute;
  top:0;
  right:0;
  height:2px;
  width:0;
  background: #1AAB8A;
  transition:400ms ease all;
}
#languageToggler:after{
  right:inherit;
  top:inherit;
  left:0;
  bottom:0;
}
#languageToggler:hover:before,#languageToggler:hover:after{
  width:100%;
  transition:800ms ease all;
}

.clicked {
  display: none;
}
</style>



