import Vue from 'vue';
import VueI18n from 'vue-i18n';

Vue.use(VueI18n);
const messages = {
    'en': {
    	employees: "Employees",
    	navbar: "Employee Manager",
    	joinButton: "Join us",
    	whyJoinUs: "Why join us",
    	submit: "Submit",
    	newEmployee: "New Employee",
    	employeeIdLabel: "Employee ID",
    	listOfWishers: "List of wishers"
    },
    'ru': {
    	employees: "Сотрудники",
    	navbar: "Менеджер Сотрудников",
    	joinButton: "Стать одним из нас",
    	whyJoinUs: "Наши преимущества",
    	submit: "Подтвердить",
    	newEmployee: "Новый сотрудник",
    	employeeIdLabel: "ID сотрудника",
    	listOfWishers: "Список желающих"
    }
};
const i18n = new VueI18n({
    locale: 'en', // set locale
    fallbackLocale: 'ru', // set fallback locale
    messages, // set locale messages
});
export default i18n


