<template>
	<div id="last-actions">
		<ul class="collection with-header">
			<li class="collection-header"> <h3>{{ $t('listOfWishers') }}</h3></li>
		<li class="collection-item" v-for="wisher in wishers_name">
			<span>Name:</span>

			{{wisher.name}}
			<p>Wants to: {{wisher.dept}}</p>
		</li>
			<button @click="approveWisher">Approve everyone</button>
	    </ul>
	</div>
</template>
<script>
	import db from './firebaseInit'
	export default {
		name: 'last-actions',
		data() {
			return {
				wishers_name: [],
				afterValidation: []
			}
		},
		created() {
				db.collection('join_wishers').get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						var random = Math.floor((Math.random() * 1000) + 1);
						const data = {
							'name': doc.data().name,
							'dept': doc.data().wantsTo,
							'employee_id': random
						}
						this.wishers_name.push(data)
					})
				})
			},
			methods: {
				approveWisher() {
					//for(var wisher in this.wishers_name) {
						  //console.log(wisher.name)
					//}
					this.wishers_name.forEach(name => {
						var data2 = {
							'name': name.name,
							'dept': name.dept,
							'employee_id':name.employee_id
						}
						this.afterValidation.push(data2)
					})
					this.afterValidation.forEach(test =>{
						var call = test.name
						var call1 = test.dept
						var call2 = test.employee_id
						db.collection('employees').add({
					    name: call,
					    dept: call1,
					    employee_id: call2
				})
					.then(db.collection('join_wishers').get().then(querySnapshot => {
						querySnapshot.forEach(doc => {
							 doc.ref.delete()
							})
					})).then(docRef => this.$router.push('/'))
					})
				}
			}
		}
</script>



